                <button class="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                    <span class="navbar-toggler-icon"></span>
                </button>
                <div class="collapse navbar-collapse" id="navbarSupportedContent">
                    <ul class="navbar-nav ms-auto">
                        <li class="nav-item"><a href="#homeSection" class="nav-link">Home</a></li>
                        <li class="nav-item"><a href="#skillSection" class="nav-link">About</a></li>
                        <li class="nav-item"><a href="#portofolioSection" class="nav-link">Skill</a></li>
                        <li class="nav-item"><a href="#contactSection" class="nav-link">Contact</a></li>
                    </ul>
